* {
    padding: 0;
    margin: 0;
}


body {
    color: white;
    background-color: #333;
}

.time-sets {
    display: grid;
    grid-template-columns: 50px 200px 50px;
    justify-items: center;
    align-items: center;
}

.label {
    display: grid;
    grid-template-columns: 1fr 1fr;
}